@import '../../sass/variables';

/* Image */
.reports_cover {
    display: block;
    box-shadow: 0 5px 7px 0px rgba(0, 0, 0, 0.3);
    margin: 0 auto 1em;
}
  
/* Footer */
.reports_docs {
    text-align: center;
    font-size: 11px;
    text-transform: uppercase;
    background: $q4-darkGrey;
    border-radius: 5px;
    padding: 1em 0;
    line-height: 18px;

    .file_info {
        color: $fileInfo-grey;
    }
}

